import { useQuery } from "@tanstack/react-query";
import {
  getAllCategories,
  getAllCategoryDetails,
  getAllFeaturedCategories,
  getAllFeaturedProductTypes,
  getAllFeaturedSubCategories,
  getAllProductTypes,
  getAllSubcategories,
  getCategory,
  getProductType,
  getProductTypes,
  getProductTypesBasedOnBrand,
  getSubcategories,
  getSubcategory,
} from "../api/categoryApis";

const useCategoryDetails = () => {
  return useQuery({
    queryKey: ["categoryDetails"],
    queryFn: getAllCategoryDetails,
    staleTime: 1000 * 60 * 5,
  });
};

const useCategories = () => {
  return useQuery({
    queryKey: ["categories"],
    queryFn: getAllCategories,
    staleTime: 1000 * 60 * 5,
  });
};

const useAllFeaturedCategories = (options) => {
  return useQuery({
    queryKey: ["categories", "featured"],
    queryFn: getAllFeaturedCategories,
    staleTime: 1000 * 60 * 5,
    ...options,
  });
};

const useCategoryById = (categoryId) => {
  return useQuery({
    queryKey: ["category", categoryId],
    queryFn: () => getCategory(categoryId),
    staleTime: 1000 * 60 * 5,
    enabled: !!categoryId,
  });
};

const useSubcategories = () => {
  return useQuery({
    queryKey: ["subcategories"],
    queryFn: getAllSubcategories,
    staleTime: 1000 * 60 * 5,
  });
};

const useSubcategoriesBasedOnCategory = (categoryId) => {
  return useQuery({
    queryKey: ["subcategories", categoryId],
    queryFn: () => getSubcategories(categoryId),
    staleTime: 1000 * 60 * 5,
    enabled: !!categoryId,
  });
};

const useAllFeaturedSubcategories = () => {
  return useQuery({
    queryKey: ["subcategories", "featured"],
    queryFn: getAllFeaturedSubCategories,
    staleTime: 1000 * 60 * 5,
  });
};

const useSubcategoryById = (subcategoryId) => {
  return useQuery({
    queryKey: ["subcategory", subcategoryId],
    queryFn: () => getSubcategory(subcategoryId),
    staleTime: 1000 * 60 * 5,
    enabled: !!subcategoryId,
  });
};

const useProductTypes = () => {
  return useQuery({
    queryKey: ["productTypes"],
    queryFn: getAllProductTypes,
    staleTime: 1000 * 60 * 5,
  });
};

const useProductTypesBasedOnSubcategory = (subcategoryId) => {
  return useQuery({
    queryKey: ["productTypes", subcategoryId],
    queryFn: () => getProductTypes(subcategoryId),
    staleTime: 1000 * 60 * 5,
    enabled: !!subcategoryId,
  });
};

const useAllFeaturedProductTypes = () => {
  return useQuery({
    queryKey: ["productTypes", "featured"],
    queryFn: getAllFeaturedProductTypes,
    staleTime: 1000 * 60 * 5,
  });
};

const useProductTypesBasedOnBrand = (brandId) => {
  return useQuery({
    queryKey: ["productTypes", brandId],
    queryFn: () => getProductTypesBasedOnBrand(brandId),
    staleTime: 1000 * 60 * 5,
    enabled: !!brandId,
  });
};

const useProductTypeById = (productTypeId) => {
  return useQuery({
    queryKey: ["productType", productTypeId],
    queryFn: () => getProductType(productTypeId),
    staleTime: 1000 * 60 * 5,
    enabled: !!productTypeId,
  });
};

export {
  useCategoryDetails,
  useCategories,
  useCategoryById,
  useSubcategories,
  useSubcategoriesBasedOnCategory,
  useAllFeaturedSubcategories,
  useSubcategoryById,
  useProductTypes,
  useProductTypesBasedOnSubcategory,
  useAllFeaturedCategories,
  useAllFeaturedProductTypes,
  useProductTypesBasedOnBrand,
  useProductTypeById,
};
