import Dropdown from "../../components/Dropdown";
import { useCategoryDetails } from "../../services/categoryServices";
import useMediaQuery from "../../hooks/useMediaQuery";
import SubCategoryNav from "./SubCategoryNav";
import Skeleton from "../../components/Skeleton";

const CategoryNav = () => {
  const { data: categories = [], isLoading } = useCategoryDetails();
  const maxLg = useMediaQuery("(min-width: 1024px)");

  return (
    maxLg && (
      <section className="border-b border-secondary w-full">
        <div className="container max-w-full">
          <ul className="relative flex gap-8 w-max m-auto">
            {isLoading
              ? Array.from({ length: 6 }).map((_, index) => (
                  <li
                    key={index}
                    className="h-12 flex items-center justify-center"
                  >
                    <div className="w-16 h-4 rounded overflow-hidden">
                      <Skeleton />
                    </div>
                  </li>
                ))
              : categories.map((category, index) => (
                  <li key={index} className="h-12 capitalize font-medium">
                    <Dropdown
                      url={`/category/${category._id}`}
                      animation="highlight"
                      content={
                        <SubCategoryNav
                          subCategories={category.subCategories}
                        />
                      }
                    >
                      {category.name}
                    </Dropdown>
                  </li>
                ))}
          </ul>
        </div>
      </section>
    )
  );
};

export default CategoryNav;
